.l-header {
  top: 0;
  left: 0;
  right: 0;
  margin: 0;
  width: 100%;
  z-index: 500;
  position: relative;
  height: 80px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  background-color: transparent;
  transition: 0.2s;
  color: white;
  text-shadow: 0px 0px 4px rgb(0, 0, 0);
  position: fixed;
}
.l-header .l-header__buttonswrapper {
  text-shadow: none;
}
.l-header:not(.l-header.open).l-header__float {
  position: fixed;
  color: black;
  text-shadow: none;
}
.l-header:not(.l-header.open).l-header__float .l-header__content {
  background-color: white;
  opacity: 0.8;
}
.l-header.dwis-active {
  position: fixed;
  top: 0;
}

body.dwis-active {
  position: fixed;
}

.l-header__content {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  transition: 0.3s;
}

.l-header__inner {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-width: 1280px;
  margin: auto;
  width: 100%;
  height: 100%;
}

.l-header__buttonswrapper {
  display: flex;
  flex-direction: row;
  margin: auto;
  margin-right: 0;
  width: auto;
  height: 100%;
  color: black;
  font-weight: bold;
  opacity: 1;
}

.l-header__background {
  position: absolute;
  height: 100%;
  width: 100%;
  background: rgb(255, 255, 255);
}
@media screen and (min-width: 1280px) {
  .l-header__background {
    opacity: 0.4;
  }
}
@media screen and (max-width: 1279px) {
  .l-header__background {
    opacity: 1;
  }
}

.l-header__nav-wrapper {
  width: 100%;
}
.l-header__nav {
  margin: auto auto auto auto;
  display: flex;
  height: 100%;
