  display: flex;
  flex-direction: row;
  position: relative;
  font-size: 18px;
  height: 100%;
  width: 100%;
  padding-left: 18px;
  padding-top: 4px;
  border-bottom: 1px solid black;
}
.p-faq__grid .p-faq__griditem .p-faq__q:before {
  content: "Ｑ";
  width: 16px;
  height: 100%;
  transform: translate(-4px, 0px);
  line-height: 26px;
  color: goldenrod;
  margin: auto;
  margin-right: 20px;
  margin-top: 0;
