  margin: auto;
  margin-left: 1px;
  margin-top: -2px;
  padding-top: 20px;
  padding-bottom: 21px;
}
.p-faq__grid .p-faq__griditem .p-faq__q .p-faq__q-inner .icon {
  margin-right: 26px;
}
.p-faq__grid .p-faq__griditem .p-faq__q:after {
  position: absolute;
  content: "";
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border-bottom: 1px solid black;
}
.p-faq__grid .p-faq__griditem .p-faq__a {
  display: flex;
  flex-direction: row;
  position: relative;
  font-size: 18px;
  height: 100%;
  width: 100%;
  overflow: hidden;
}
.p-faq__grid .p-faq__griditem .p-faq__a[data-open=true] .p-faq__a__inner p {
  transform: translate(0, 0%);
