  position: relative;
  display: flex;
  flex-direction: row;
  margin: auto;
  margin-left: 0;
  padding-top: 16px;
  padding-left: 18px;
  font-weight: 400;
  font-size: 16px;
}
.p-faq__grid .p-faq__griditem .p-faq__a .p-faq__a__inner p {
  transition: transform 0.3s;
  transform: translate(0, -50%);
  display: block;
