.js-gotoTop__Wrapper {
  position: fixed;
  width: 60px;
  height: 60px;
  right: 0px;
  bottom: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: transparent;
  z-index: 100;
}

.js-gotoTop {
  position: fixed;
  width: 60px;
  height: 60px;
  right: 0px;
  bottom: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: lightgreen;
  z-index: 100;
  transition: 0.3s;
  opacity: 1;
  cursor: pointer;
}
.js-gotoTop.hide {
  pointer-events: none;
  opacity: 0;
}
.js-gotoTop:hover {
  transform: translate(0, -2px);
  -webkit-filter: drop-shadow(rgb(0, 0, 0) 0px 1px 2px);
  background-color: #FFFFFF;
}
.js-gotoTop:hover .js-gotoTop__icon {
  filter: invert(80%);
}
@media screen and (max-width: 767px) {
  .js-gotoTop {
    right: 10px;
    bottom: 4px;
  }
  .js-gotoTop:hover {
    bottom: 10px;
  }
}

.js-gotoTop__icon {
  position: relative;
  display: block;
  width: 40px;
  height: 40px;
  text-decoration: none;
  filter: invert(0%);
  transition: 0.3s;
  font-size: 25px;
  color: #fff;
  margin: auto;
}

.js-gsap__scrollbutton {
  width: 100px;
  bottom: 15vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.js-gsap__scrollbutton .icon {
  width: 100px;
  height: 100px;
  display: flex;
  transform: translate(0%, 0%);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
}
.js-gsap__scrollbutton .icon span {
  opacity: 0;
  position: absolute;
  width: 20px;
  height: 20px;
  border: 4px solid #7EAB71;
  border-top: none;
  border-left: none;
  transform: rotate(45deg);
}
.js-gsap__scrollbutton .text {
  margin: auto;
  margin-bottom: 0;
  display: flex;
