/*--------------------------------------------------
# 上記resetcssで特殊なものを処理し、いままで使っていたresetを使う
# フォームリセット
# 全体を通して不要な設定を取り除く
--------------------------------------------------*/

@charset "UTF-8";
*,
*::before,
*::after {
    box-sizing: border-box;
}

html,
body {
    margin: 0;
    padding: 0;
}

article,
aside,
footer,
header,
nav,
section,
main {
    display: block;
}

h1,
h2,
h3,
h4,
h5,
h6,
a,
p,
span,
em,
small,
strong,
sub,
sup,
mark,
del,
ins,
strike,
abbr,
dfn,
blockquote,
q,
cite,
code,
pre,
ol,
ul,
li,
dl,
dt,
dd,
div,
section,
article,
main,
aside,
nav,
header,
hgroup,
footer,
img,
figure,
figcaption,
address,
time,
audio,
video,
canvas,
iframe,
details,
summary,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
    border: 0;
    margin: 0;
    padding: 0;
}

a {
    color: inherit;
    //outline: none;
    text-decoration: none;
}

img {
    border: none;
    height: auto;
    max-width: 100%;
    vertical-align: top;
}

code,
kbd,
pre,
samp {
    font-family: monospace, sans-serif;
    font-size: inherit;
}

ul,
ol {
    list-style: none;
}

